@import "../../assets/scss/common.scss";
.ResultSearch {
  display: flex;
  flex: 1;
  flex-direction: column;
  margin: 0px 5%;
  .BodySearch {
    display: flex;
    flex: 1;
    flex-direction: column;
    .ProductList {
      display: flex;
      flex: 1;
      flex-direction: row;
      flex-wrap: wrap;

      position: relative;
      justify-content: center;
      min-height: 400px;
      .ContainerLoading {
        display: flex;
        flex: 1;
        width: 100%;
        justify-content: center;
        align-items: center;
        .Loading {
          width: 100%;
          height: 100%;
          position: absolute;
          background-color: $textColor;
          opacity: 0.5;
          z-index: 20;
          left: 0px;
          top: 0px;
        }
        .ImgLoading {
          position: absolute;
          align-self: auto;
          margin: auto;
          justify-self: center;
          width: 150px;
          height: 150px;
          // left: 47%;
          // top: 47%;
          z-index: 24;
        }
      }
    }

    .Pagination {
      display: flex;
      flex: 1;
      flex-direction: row;
      justify-content: center;
      padding: 5px;
    }
  }
}
